import React, { useEffect, useState } from "react";
import Slider from "react-slick";
import Dental_Diagnosis from "../../assets/services_img/Dental diagonis.png";
import Restorative_Dentistry from "../../assets/services_img/Restorative Dentistry.png";
import Root_Canal from "../../assets/services_img/Root Canals & Crowns.png";
import Children_Dentistry from "../../assets/services_img/Child.png";
import Teeth_Alignment from "../../assets/services_img/Teeth Alignment.png";
import Teeth_Restoration from "../../assets/services_img/Teeth Restoration & Replacement.png";
import Gum_infection from "../../assets/services_img/Gum Disease Treatment.png";
import Aesthe from "../../assets/services_img/Aesthe.png";
import Wisdom_Teeth from "../../assets/services_img/Wisdom Teeth Removal.png";
import Dental_Bridges from "../../assets/Dental Bridges.png";
import Crowns from "../../assets/Crowns.png";
import Complete_Denture from "../../assets/Complete Denture.png";
import ceramic_brames from "../../assets/ceramic brames.png";
import Bleeding_gums from "../../assets/Bleeding gums.png";
import X_rays from "../../assets/X rays 1.png";
import Veneer from "../../assets/Veneer.png";
import Tooth_removal from "../../assets/Tooth removal.png";
import Tooth_Jewellery from "../../assets/Tooth Jewellery.png";
import Self_ligating_braces from "../../assets/Self-ligating braces.png";
import "./CarouselShow4.scss";
import AOS from "aos";
import "aos/dist/aos.css";
import { useHistory } from "react-router-dom";

export default function CarouselShow4() {
  useEffect(() => {
    AOS.init();
  }, []);

  let history = useHistory();

  const NextArrow = ({ onClick }) => {
    return (
      <div className="arrows next_button" onClick={onClick}>
        <button
          style={{ outline: "none" }}
          type="submit"
          className="previous2 round2"
        >
          &#8250;
        </button>
        {/* <h1>Next</h1> */}
      </div>
    );
  };

  const PrevArrow = ({ onClick }) => {
    return (
      <div className="arrows prev_button" onClick={onClick}>
        <button
          style={{ outline: "none" }}
          type="submit"
          className="previous1 round1"
        >
          &#8249;
        </button>
        {/* <h1>Prev</h1> */}
      </div>
    );
  };
  const images1 = [
    {
      pics: Dental_Diagnosis,
      heading: "Dental Diagnosis",
      subheading: "Finding out what’s wrong",
    },
    {
      pics: Restorative_Dentistry,
      heading: "Restorative Dentistry",
      subheading: "Filling of Broken and Decayed Teeth",
    },
    {
      pics: Root_Canal,
      heading: "Root Canals & Crowns",
      subheading: "Saving the teeth",
    },
    {
      pics: Gum_infection,
      heading: "Gum Disease Treatment",
      subheading: "Care for the gums that hold your teeth.",
    },
    {
      pics: Teeth_Alignment,
      heading: "Teeth Alignment",
      subheading: "Putting your teeth where they belong",
    },
    {
      pics: Teeth_Restoration,
      heading: "Teeth Restoration & Replacement",
      subheading: "Restoring and replacing damaged teeth.",
    },
    {
      pics: Aesthe,
      heading: "Aesthe",
      subheading: "Making your smile charming.",
    },
    {
      pics: Wisdom_Teeth,
      heading: "Wisdom Teeth Removal",
      subheading: "Making your smile charming.",
    },
    {
      pics: Children_Dentistry,
      heading: "Childrens Dentistry",
      subheading: "Care for the little ones to keep smiling.",
    },
  ];

  const [imageIndex, setImageIndex] = useState(0);
  const settings = {
    infinite: true,
    lazyLoad: true,
    speed: 300,
    slidesToShow: 4,
    centerMode: false,
    centerPadding: 0,
    nextArrow: <NextArrow />,
    prevArrow: <PrevArrow />,
    beforeChange: (current, next) => setImageIndex(next),
    responsive: [
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          infinite: true,
        },
      },
      {
        breakpoint: 800,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
        },
      },
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 1,
          infinite: true,
        },
      },
    ],
  };
  return (
    <div>
      <div className="container-fluid p-0 mb-5">
        <div className="carouselShow4_onevisit col-12">
          <Slider {...settings}>
            {images1.map((img, idx) => (
              <div
                className={
                  idx === imageIndex ? "slides activeSlides" : "slides"
                }
              >
                <div className="patch">
                  <h1>{img.heading}</h1>
                  <p>{img.subheading}</p>
                  <button
                    className="btn btn-primary"
                    style={{ fontFamily: "si-ui-light", letterSpacing: "2px" }}
                    onClick={() => {
                      history.push({
                        pathname: "/services",
                        state: { treatmentindex: idx },
                      });
                    }}
                  >
                    Explore
                  </button>
                </div>
                <img className="pics" src={img.pics} />
              </div>
            ))}
          </Slider>
        </div>
        <div class="three-dots">
          {images1.map((img, idx) => {
            return <span class={idx == imageIndex ? "second" : "first"}></span>;
          })}
        </div>
      </div>
    </div>
  );
}
