import React, { useEffect, useState } from "react";
import "./CarouselShow3.scss";
import Slider from "react-slick";
// import astronaut from "../../assets/Group 693.png";
// import celebrating from "../../assets/Group 691.png";
// import education from "../../assets/Group 690.png";
import AOS from "aos";
import "aos/dist/aos.css";

export default function CarouselShow3(props) {
  useEffect(() => {
    AOS.init();
  }, []);

  const NextArrow = ({ onClick }) => {
    return (
      <div className="arrows next_button_services" onClick={onClick}>
        <button
          style={{ outline: "none" }}
          type="submit"
          className="previous2 round2"
        >
          &#8250;
        </button>
        {/* <h1>Next</h1> */}
      </div>
    );
  };
  const PrevArrow = ({ onClick }) => {
    return (
      <div
        className="arrows prev_button_services"
        onClick={onClick}
        style={{ marginTop: "-300px", marginLeft: "10px" }}
      >
        <button
          style={{ outline: "none" }}
          type="submit"
          className="previous1 round1"
        >
          &#8249;
        </button>
        {/* <h1>Prev</h1> */}
      </div>
    );
  };
  // const images = [astronaut, celebrating, education, celebrating];
  const images = [props.slider1, props.slider2, props.slider3, props.slider1];

  const [imageIndex, setImageIndex] = useState(0);
  const settings = {
    infinite: true,
    lazyLoad: true,
    speed: 300,
    slidesToShow: 3,
    centerMode: true,
    centerPadding: 0,
    nextArrow: <NextArrow />,
    prevArrow: <PrevArrow />,
    beforeChange: (current, next) => setImageIndex(next),
    responsive: [
      {
        breakpoint: 1600,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
          centerMode: true,
          centerPadding: "0px",
        },
      },
      {
        breakpoint: 2000,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
          centerMode: true,
          centerPadding: "100px",
        },
      },
    ],
  };
  return (
    <div>
      <div
        className="carasouel_services container-fluid p-0"
        data-aos="zoom-in"
        data-aos-duration="3000"
      >
        <Slider {...settings}>
          {images.map((img, idx) => (
            <div
              className={
                idx === imageIndex
                  ? "slide_services activeSlide_services"
                  : "slide_services"
              }
            >
              <img src={img} alt={img} className="img-fluid" />
            </div>
          ))}
        </Slider>
      </div>
    </div>
  );
}
